import { useNavigate } from 'react-router-dom';
import style from './style.module.css';
import React from 'react';
import Button from '../shared/button/Button';

const About: React.FC = () => {
    const backText = ' ';

    const navigate = useNavigate();

    return (
        <main className={style.about}>
            <h3> </h3>
            <label className={style.about__info}>     Fun Chat    RSSchool JS/FE 2023Q3</label>
            <label className={style.about__info}>        </label>
            <a className={style.about__link} href='https://github.com/MikAleinik'> MikAleinik</a>
            <Button
                text={backText}
                callback={() => navigate('/main')}
            />
        </main>
    );
};

export default About;